	module.exports = "<div class=container-fluid><div class=row><div class=\"col-md-offset-3 col-md-6\"><h2>Restaurants</h2></div></div><div class=row><div class=col-md-3><div class=form-group><label>Cuisine</label><select ng-model=filterCuisine><option value=\"\">Any<option ng-repeat=\"option in categories\" value={{option}}>{{option}}</select></div><div class=form-group><label>Order Type</label><div><label><input type=radio ng-model=filterOrderType value=either name=orderType id=option3 autocomplete=off> Either</label></div><div><label><input type=radio ng-model=filterOrderType value=delivery name=orderType id=option1 autocomplete=off default> Delivery</label></div><div><label><input type=radio ng-model=filterOrderType value=carryout name=orderType id=option2 autocomplete=off> Carry-out</label></div></div><div class=form-group><label>Sort By</label><div><label><input type=radio ng-model=sortMode value=+name name=sortMode id=option1 autocomplete=off default> Name</label></div><div><label><input type=radio ng-model=sortMode value=-rating name=sortMode id=option2 autocomplete=off> Rating</label></div><div><label><input type=radio ng-model=sortMode value=+distance name=sortMode id=option3 autocomplete=off> Distance</label></div><div><label><input type=radio ng-model=sortMode value=+price name=sortMode id=option3 autocomplete=off> Price</label></div></div><div class=form-group><label>Rating</label><select ng-model=filterRating><option value=0>Any<option value=2>2 Star<option value=3>3 Star<option value=4>4 Star<option value=5>5 Star</select></div><div class=form-group><label>Price</label><select ng-model=filterPrice><option value=1000>Any<option value=5>$<option value=10>$$<option value=15>$$$<option value=20>$$$$<option value=25>$$$$$</select></div><div class=form-group><label>Distance</label><select ng-model=filterDistance><option value=1000>Any<option value=1>Walking distance (1 mile)<option value=3>3 miles<option value=5>5 miles<option value=10>Driving distance (10 miles)<option value=15>Road trip (15 miles)</select></div><div class=form-group><label>Has Discounts <input type=checkbox ng-model=filterDiscounts></label></div></div><div class=col-md-6><div class=row><div class=col-md-12 ng-show=\"(restaurants | filter:masterFilter).length == 0\"><h3>Sorry, no restaurants fit your criteria</h3></div></div><div class=\"row restaurant-row\" ng-repeat=\"restaurant in restaurants | filter:masterFilter | limitTo:30 | orderBy:sortMode\"><div class=col-md-4><div>{{restaurant.name}}</div><div ng-if=\"filterCuisine == ''\" class=restaurantCuisine><small>{{restaurant.cuisine}}</small></div><div class=restaurant-stars><span ng-repeat=\"i in range(5)\"><i class=glyphicon ng-class=\"{'glyphicon-star': $index < restaurant.rating, 'glyphicon-star-empty': $index >= restaurant.rating}\"></i></span></div></div><div class=col-md-2>{{restaurant.distance | number:1}} mi<div class=restaurant-price><span ng-repeat=\"i in range((restaurant.price-1)/5)\">$</span></div></div><div class=\"col-md-4 restaurant-notices\"><div ng-if=restaurant.hasDiscounts><i class=\"glyphicon glyphicon-tag\"></i> Discounts available</div><div ng-if=\"filterOrderType == 'either' && restaurant.orderType != 'either'\"><em>{{restaurant.orderType}} only</em></div></div><div class=col-md-1><a href=#/menu ng-click=\"foodService.selectedRestaurant = restaurant\" class=\"btn btn-primary\">Select</a></div></div></div></div></div>";

